.homepage .home__container.hasGradient {
  background: linear-gradient(190deg, #473bff 25%, #2e7ffe 55%, #01aefe 100%);
  margin-top: 0; }

.homepage .home__container {
  min-height: 750px;
  overflow: hidden;
  position: relative;
  text-align: center;
  display: flex;
  align-items: center; }
  .homepage .home__container::after {
    background-image: url("/static/img/wave.svg");
    background-position-x: center;
    background-position-y: top;
    background-repeat: no-repeat;
    background-size: 5223px 186px;
    width: 100%;
    height: 238px;
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    transform: scale(2.7);
    -webkit-transform: scale(2.7);
    -moz-transform: scale(2.7);
    -ms-transform: scale(2.7);
    -o-transform: scale(2.7); }
  .homepage .home__container .home-video {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.2; }
  .homepage .home__container .home__content {
    position: absolute;
    z-index: 8; }
    .homepage .home__container .home__content .home__content__wrapper {
      margin: 0 auto;
      width: 62%; }
      .homepage .home__container .home__content .home__content__wrapper .home__content__container .content__title {
        color: white;
        font-size: 5rem;
        font-weight: bold;
        text-decoration: none; }
      .homepage .home__container .home__content .home__content__wrapper .home__content__container .content__subtitle {
        color: white;
        font-size: 2.2rem;
        font-weight: 400;
        text-decoration: none;
        margin-top: 2rem; }
      .homepage .home__container .home__content .home__content__wrapper .home__content__container .content__signup {
        margin-top: 4rem; }
        .homepage .home__container .home__content .home__content__wrapper .home__content__container .content__signup .content__btn-signup {
          background: #e625a4;
          color: white;
          font-size: 1.4rem;
          padding: 1.7rem 3.5rem;
          border-radius: 2.5rem;
          -webkit-border-radius: 2.5rem;
          -moz-border-radius: 2.5rem;
          -ms-border-radius: 2.5rem;
          -o-border-radius: 2.5rem;
          font-weight: bold;
          text-decoration: none; }
          .homepage .home__container .home__content .home__content__wrapper .home__content__container .content__signup .content__btn-signup:hover {
            background: #e44fb0;
            color: white;
            font-size: 1.4rem;
            padding: 1.7rem 3.5rem;
            border-radius: 2.5rem;
            -webkit-border-radius: 2.5rem;
            -moz-border-radius: 2.5rem;
            -ms-border-radius: 2.5rem;
            -o-border-radius: 2.5rem; }

@media screen and (max-width: 1063px) {
